                    }
                }
            };
            // Bank B Leaf Speed Error
            var trace4 = {
                x: this.error.mlc.vel_b,
                xbins: {
                    end: 0.50, 
                    size: 0.001, 
                    start: -0.50
                },
                name: 'Bank B Leaf Speed Error',
                autobinx: false,
                type: 'histogram',
                opacity: 0.75,
                marker: {
                    color: "rgba(100, 200, 102, 0.7)",
                    line: {
                        color:  "rgba(100, 200, 102, 1.0)", 
